import React from 'react';
import { Link } from 'react-router-dom';
import { Helmet } from 'react-helmet';
import Aux from '../../hoc/_Aux/_Aux';
import resumePath from '../../assets/images/codyLuke_resume.pdf';

const about = props => {
	return (
		<Aux>
			<Helmet>
				<title>About | Cody</title>
				<meta name="description" content="Dan Beltramo - About Me" />
			</Helmet>
				<div className="intro w1">
					<h4 className="title ">Cody Luke</h4>
					<h1 className="brandDescription short">About me</h1>
				<p className="projectCopy blackText w2">
				Cody Luke was born and raised in San Francisco. She is currently based in Portland, Oregon, and is working as a Store Designer for Nike. She is well-versed in developing design concepts and interior details for high-end retail environments. She loves to stay up to date with current trends, color, technology, and related design influencers. In her free
				time, she is currently remodeling her carriage house.</p>
				<div className="resume w1 m-t-20 m-b-90">
					<a href={resumePath} target="_blank" rel="noopener noreferrer">
						<p className="pink">Resume</p>
					</a>
			</div>
			</div>
		</Aux>
	);
};

export default about;
