import React from 'react';
import { Link } from 'react-router-dom';
import { Helmet } from 'react-helmet';
import Aux from '../../hoc/_Aux/_Aux';

const services = props => {
	return (
		<Aux>
			<Helmet>
				<title>Services | Cody</title>
				<meta name="description" content="Dan Beltramo - About Me" />
			</Helmet>
			<div>
			<div className="intro w1 servicesGroup">
			<div className="categories services">
				<h5>Kitchen design</h5>
				<h5>Bath design</h5>
				<h5>Plan layout reworking</h5>
				<h5 className="desktop">Detail development</h5>
			</div>
				<h1 className="brandDescription short">Design Services</h1>
<h2 class="categoryTitle blackText  serviceTitle">Conceptual Design</h2>
<p className="projectCopy blackText  serviceCopy">
			In this early design phase, Im happy to partner to help clarify your design vision, looking at the project holistically within its greater context. I can help refine your design direction, using conceptual imagery, inspiration boards, early renderings, and frequent and casual design reviews.</p>

<h2 class="categoryTitle blackText  serviceTitle">Design Development</h2>
<p className="projectCopy blackText  serviceCopy">
			As the design becomes more developed, I can propose options for fixture selection (overall lighting, bath elements, kitchen elements, furniture, storage, kitchen, built ins, etc). I can help select paint colors, spatial modifications (removing walls etc), as well as the initial design of custom features. To help establish a consensus or chosen direction, I can develop high-fidelity digital renderings of spaces.
</p>
			<h2 class="categoryTitle blackText  serviceTitle">Design Handoff</h2>
			<p className="projectCopy blackText  serviceCopy">
			As design nears the handoff to construction (contractor or otherwise), I can produce design drawings which represent design intent, including lists of specifications of what to order. In some cases, I can help source these as well, as I have access to special discounts, as well as knowledge of local producers and fabricators in the Pacific Northwest.
</p>
<p className="projectCopy blackText  serviceCopy">
			I have a keen eye for furniture selection, color pairings, and precise pairing design details, as well as an active awareness of current trends, color, technology, and other related design influencers.
</p>
<a href="mailto:codyluke003@gmail.com">
<button className="projectDetails">
	Say hi &nbsp; &#8594;
</button>
</a>
</div>
<div className="webPhoto">
<img src={require(`../../assets/images/cody-kitchen2.jpg`)} width="100%" alt="alt" />
</div>
			</div>
		</Aux>
	);
};

export default services;
